import { connect } from 'react-redux'
import { getTrackDetailsAsync, getRequestDetailsAsync, cancelRequestAsync, servicefeedbackAsync,
  rescheduleSlotsAsync, rescheduleRequestAsync, getInvoiceAsync, updateEstimationAsync, clearTrackDetails,
  setDeviceDataforRequest, initiatePaymentAsync, setPaymentDetails, getSignedUrlAsync,
  berModeSelectionAsync, fetchBERModeListAysnc, updateAccessoriesStatusAsync,
  addUploadedImageToPendingDocsAsync, getLogisticsDetailsAsync, checkInVicinity,
  sendLogisticEmailAsync } from '../../../store/track'
import { storeLastKnowPathName } from '../../../store/location'
import { getProductDocsAsync, pendingDocsListAsync, getDevicesAsync } from '../../../store/devices'
import { getNotificationsAsync, postPlanAction } from '../../../store/user'
import { setToaster, setglobalLoaderStatus } from '../../../store/toastsAndLoaders'
import { addToVisitedUrl, setRequestFlowSource, getRequestModesAsync,
  getRequestSlotsAsync, convertDropOffToPickupAsync, setSelectedDevice,
  setSelectedMode, setUserRequestLocation, setSelectedDamageType } from '../../../store/request'

/*  This is a container component. Notice it does not contain any JSX,
    nor does it import React. This component is **only** responsible for
    wiring in the actions and state necessary to render a presentational
    component - in this case, the counter:   */

import Track from '../components/Track'

/*  Object of action creators (can also be function that returns object).
    Keys will be passed as props to presentational components. Here we are
    implementing our wrapper around increment; the component doesn't care   */

const mapDispatchToProps = {
  getTrackDetailsAsync,
  getRequestDetailsAsync,
  cancelRequestAsync,
  rescheduleSlotsAsync,
  rescheduleRequestAsync,
  getInvoiceAsync,
  updateEstimationAsync,
  servicefeedbackAsync,
  setDeviceDataforRequest,
  sendLogisticEmailAsync,
  getNotificationsAsync,
  initiatePaymentAsync,
  postPlanAction,
  setToaster,
  setPaymentDetails: (obj) => setPaymentDetails(obj),
  clearTrackDetails: () => clearTrackDetails(),
  fetchBERModeListAysnc,
  berModeSelectionAsync,
  getProductDocsAsync,
  updateAccessoriesStatusAsync,
  pendingDocsListAsync,
  getSignedUrlAsync,
  addUploadedImageToPendingDocsAsync,
  getLogisticsDetailsAsync,
  getDevicesAsync,
  addToVisitedUrl: (obj) => addToVisitedUrl(obj),
  setRequestFlowSource: (obj) => setRequestFlowSource(obj),
  checkInVicinity,
  getRequestModesAsync,
  getRequestSlotsAsync,
  convertDropOffToPickupAsync,
  setSelectedDevice: (obj) => setSelectedDevice(obj),
  setSelectedMode: (obj) => setSelectedMode(obj),
  setUserRequestLocation: (obj) => setUserRequestLocation(obj),
  setglobalLoaderStatus: (bool) => setglobalLoaderStatus(bool),
  storeLastKnowPathName: (obj) => storeLastKnowPathName(obj),
  setSelectedDamageType: (obj) => setSelectedDamageType(obj)
}

const mapStateToProps = (state) => ({
  user: state.user,
  track: state.track,
  devices: state.devices,
  request: state.request
})

/*  Note: mapStateToProps is where you should use `reselect` to create selectors, ie:

    import { createSelector } from 'reselect'
    const counter = (state) => state.counter
    const tripleCount = createSelector(counter, (count) => count * 3)
    const mapStateToProps = (state) => ({
      counter: tripleCount(state)
    })

    Selectors can compute derived data, allowing Redux to store the minimal possible state.
    Selectors are efficient. A selector is not recomputed unless one of its arguments change.
    Selectors are composable. They can be used as input to other selectors.
    https://github.com/reactjs/reselect    */

export default connect(mapStateToProps, mapDispatchToProps)(Track)



